


var getAtandanceList = function(status){
  var _self = this ;
  this.H.toAjax(true).then(function (data) { //data 公用参数
        var submitData = data;
        var page = _self.cPage ;
        var size = 4; // 动态页 10 条
        if(!status){
          status = "default" ;
        }
        submitData.data = {
          type: "all",
          page: {
            page: page,
            size: size
          }
        };
          _self.A.post("/v1/attendance/list", submitData).then(function (result) {
            console.log(result);
            if (result.data.state.code == 0 ) {
              console.info("有反悔数据");
              _self.hideLoading();
             var data =  result.data.data ;
             var activities =   result.data.data && result.data.data.activities.length ==0 ?[]: result.data.data.activities;

                if(status === "default"){


                 _self.$nextTick(() => {
                  _self.startedLessonArr = activities;
                  _self.hasMore = data.hasMore ; //是否有更多
                    _self.$nextTick(() => {
                      _self.$refs.scroller.donePulldown();
                   })
                 })

                }else if(status == "loadMore"){
                  var a = _self.startedLessonArr ;
                  var b =  activities ;


                   _self.$nextTick(() => {
                    _self.startedLessonArr = a.concat(b) ;
                     // _self.startedLessonArr.push(activities); // 添加进去
                     _self.hasMore = data.hasMore ;//是否有更多
                     if(!data.hasMore){
                      //do something ...
                     }
                      _self.$nextTick(() => {

                        _self.$refs.scroller.donePullup(); //hide 下拉加载 特效
                      })

                   })
                }




            }
          })
  }) ;
}
import Scroller from 'vux/src/components/scroller/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


export default {
  components: {
    Scroller,
    XProgress,
    Divider,
    XSwitch,
    Group,
    Spinner

  },
  methods: {
    loadMore () {
      var self = this ;
      if(!this.hasMore){
        alert("已无更多");
        self.$refs.scroller.donePullup(); //hide 下拉加载 特效
      }

      this.cPage += 1 ;

      getAtandanceList.call(this,"loadMore");


    },
    refresh () {
      var self = this ;

      this.cPage = 1 ;

      getAtandanceList.call(this);
    },
  },
  activated () {
    this.$refs.scroller.reset()
  },
  mounted(){
    this.cPage = 1 ;
    getAtandanceList.call(this,"default");
  },
  data () {
    return {
      n: 10,
      n1: 10,
      pullupEnabled: true,
      status: {
        pullupStatus: 'default',
        pulldownStatus: 'default'
      },
      pullUpObj:{  // 上拉刷新
        content: '上拉加载更多',
        downContent: '松开进行加载',
        upContent: '上拉加载更多',
        loadingContent: '加载中...',
         pullUpHeight: 60,
  height: 40,
  autoRefresh: false,
      },
      pullDownObj:{

      },
      hasMore:false,
      cPage:1,
      lessonPage:[],
      startedLessonArr:[]
    }
  }
}



